.containerFeedBack{
    margin: 50px;
    padding: 10px;
    width: 40%;
    background-color: rgb(173, 238, 216);
    box-shadow: 10px 5px 5px rgb(100, 100, 100);
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.containerLike {
    display: flex;
    flex-direction: row;
    justify-content: start;    
    align-items: center;
    gap: 20px;
}
